<template>
  <div id="common_header" :style="bg">
    <div class="common_header_safe_top"></div>
    <div class="common_header_content">
      <slot name="left"></slot>
      <slot name="center"></slot>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'commonHeader',
    props: ['bg'],
  }

</script>

<style lang="less" scoped>
  #common_header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 78px !important;
    z-index: 50;
  }
  #common_header .common_header_safe_top {
    width: 100%;
    height: 34px !important;
  }
  #common_header .common_header_content {
    position: relative;
    width: 100%;
    height: 44px !important;
  }
   .common_header_left {
    position: absolute;
    top: 0;
    left: 0;
    width: 10%;
    height: 44px !important;
    line-height: 44px !important;
    overflow: hidden;
    cursor: pointer;
    i {
      display: block;
      margin: 0 5px 0 12px !important;
      // margin: 12px auto 12px 20px !important;
      font-size: 24px !important;
      font-family: ProximaNova-Medium;
    }
  }
  .common_header_center {
    position: absolute;
    top: 0;
    left: 70px;
    width: 90%;
    height: 44px !important;
    line-height: 44px !important;
    // padding: 19px 0 19px 0;
    font-size: 16px;
    color: #000;
    text-align: left;
    font-family: ProximaNova-Medium;
  }
</style>



